import StatisticsCards from '@/components/cards/StatisticsCards';
import ShopIcon from '@/components/icons/ShopIcon';
import CalendarIcon from '@/components/icons/statistics/CalendarIcon';
import SettingsIcon from '@/components/icons/statistics/SettingIcon';
import { endpoints } from '@/lib/axios/axios-global';
import { apiGet } from '@/lib/axios/axios-utils';
import { Card } from '@/types/statistics';
import { useEffect, useState } from 'react';
import { BranchesStats } from '../../types/branchesStats';

export default function DashboardCards() {
    const [stats, setStats] = useState<BranchesStats | null>(null);

    useEffect(() => {
        const fetchStats = async () => {
            try {
                const res = await apiGet({ endpoint: endpoints.branch.dashboard });
                setStats(res.data.data);
            } catch (err) {
                setStats(null);
            }
        };
        fetchStats();
    }, []);

    const cards: Card[] = [
        { text: 'عدد الفروع', icon: <ShopIcon />, num: stats?.total, percent: 0 },
        { text: 'عدد المناطق', icon: <CalendarIcon />, num: stats?.areas, percent: -0.3 },
        { text: 'عدد عمليات الايجار', icon: <SettingsIcon />, num: 32, percent: 0.3, negative: true },
        { text: 'قيمة الايجيارات', icon: <SettingsIcon />, num: 32, percent: 0.3 },
    ];

    return <StatisticsCards cards={cards} />;
}
